.book{
    display: flex;
    flex-direction: column-reverse;
    max-width: 145px;
    position: relative;
    
}
.button-container{
    display: flex;
}
.book__button{
    width: 30px;
    display: none;
    position: absolute;
    background-color: transparent;
    cursor: pointer;    
}

.button-edit{

}
.button-delete{
    right: 0;
}
.book__text{
    font-size: 15px;
}
.book:hover .book__button{
    display: block;
}
