.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  background-color: #c0c4ce;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Book-list{
  display: flex;
  justify-content: space-between;
}

li{
  list-style: none;
}

.form__input{
  width: 250px;
  height: 25px;
  color: black;
}
button{
  border: none; 
  outline: none;
  height: 30px;
  cursor: pointer;
}
.btn-container{
  display: flex;
  justify-content: space-between;
}